import { Tabs,Layout } from "antd";
import styled from "styled-components";
import HomePageSideBar from "./HomePageSideBar";
import ProductList from"./ProductList";
const { TabPane } = Tabs;
const { Content } = Layout;
const HomePageMainContent = () => {
  return (
    <Content style={{ padding: "0 50px" }}>
      <Layout className="site-layout-background" style={{ padding: "24px 0" }}>
        <HomePageSideBar />
        <Content style={{ padding: "0 24px", minHeight: 280 }}>
          <ContentContainer>
            <Tabs>
              <TabPane tab="All" key="0"><ProductList /></TabPane>
              <TabPane tab="Food" key="1"></TabPane>
              <TabPane tab="Event" key="2"></TabPane>
              <TabPane tab="Hotel" key="3"></TabPane>
              <TabPane tab="Wellness" key="4"></TabPane>
              <TabPane tab="WorkShop" key="5"></TabPane>
              <TabPane tab="Speciality" key="6"></TabPane>
            </Tabs>
          </ContentContainer>
        </Content>
      </Layout>
    </Content>
  );
};
const ContentContainer=styled.div`
  height:auto;
  width:auto;
`;

export default HomePageMainContent;
