import React from "react";
import { Layout, Menu } from "antd";
const { SubMenu } = Menu;
const { Sider } = Layout;
function HomePageSideBar() {
  return (
    <Sider className="site-layout-background" width={200}>
      <Menu
        mode="inline"
        defaultSelectedKeys={["1"]}
        defaultOpenKeys={["sub1"]}
        style={{ height: "100%" }}
      >
        <Menu.Item key="0">All-City</Menu.Item>
        <Menu.Item key="1">Eindohoven</Menu.Item>
        <Menu.Item key="2">Tilburg</Menu.Item>
        <Menu.Item key="3">Utrecht</Menu.Item>
        <Menu.Item key="4">Delft</Menu.Item>
        <Menu.Item key="5">Haarlem</Menu.Item>
        <Menu.Item key="6">Breda</Menu.Item>
      </Menu>
    </Sider>
  );
}
export default HomePageSideBar;
